.topbar{
    font-family:'Roboto', sans-serif;
    height:50px;
    width:100%;
    display:flex;
    position:fixed;
    top:0;
    z-index:2;
    align-items:center;
    padding: 10px 20px;
    transition: all 0.2s ease-out; 
}
.topbar .icon{
    position:absolute;
    top:20%;
    left:20px;
    font-size:2.5rem;
    color:black;
    -webkit-animation:spin 2s linear infinite;
            animation:spin 2s linear infinite
}

@-webkit-keyframes spin{
    50%{
        -webkit-transform:rotateZ(180deg);
                transform:rotateZ(180deg)
    }
    100%{
        -webkit-transform:rotateZ(360deg);
                transform:rotateZ(360deg)
    }
}

@keyframes spin{
    50%{
        -webkit-transform:rotateZ(180deg);
                transform:rotateZ(180deg)
    }
    100%{
        -webkit-transform:rotateZ(360deg);
                transform:rotateZ(360deg)
    }
}

.topbar .menu-icon{
    display:none
}

.topbar .nav_items{
    display:grid;
    position:absolute;
    grid-template-columns:repeat(4, auto);
    grid-gap:2rem;
    right:150px;
    list-style-type:none;
    width:40vw;
    justify-content:space-around;
    text-transform:uppercase;
}

.topbar .nav_items a{
    padding: 30px 15px;
    text-decoration:none;
    font-size:20px;
    font-weight:bold;
    color:#000
}

.topbar .nav_items .nav_item.changehover:hover {
    background-color: #818ef0;

    transition: background-color 0.8s ease-out;
}

.topbar .nav_items .nav_item:hover{
    border-radius: 20px ;
    
}

.topbar.changebackground .nav_item:hover{
    border-radius: 0 ;
}
.topbar.changebackground {
    background: linear-gradient(135deg,  #cf171d,  #d43f44,  #3449eb,  #112bf0);
    border-bottom: 2px solid #544b4b;
    box-shadow: 0 1px 10px #000000;
}
.topbar.changebackground .icon {
    color: #fff;
}
.topbar.changebackground .menu-icon {
    color: #fff;
}
@media screen and (max-width: 900px) {
    .topbar .nav_items {
    z-index: 0;
    position: absolute;
    width: 100vw;
    top: 70px;
    left: 0;
    -webkit-transform: translate(-110%,  0);
            transform: translate(-110%,  0);
    background: linear-gradient(135deg,  #cf171d,  #3449eb,  #112bf0);
    height: calc(100vh - 62px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
    transition: all 0.4s ease-in-out;
}
.topbar .nav_items.active {
    text-align: center;
    -webkit-transform: translate(0%);
            transform: translate(0%);
}
.topbar .nav_item {
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
    padding: 20px;
    width: 100vw;
}
.topbar .nav_item a {
    padding-bottom: 20px;
    width: 30px !important;
    color: #fff;
    border-bottom: 1px dashed #fff;
}
.topbar .menu-icon {
    display: block !important;
    font-size: 2rem;
    position: absolute;
    top: 0;
    right: 70px;
    -webkit-transform: translate(10%,  50%);
            transform: translate(10%,  50%);
}
}

.intro {
    height: calc(100vh);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: url("https://images.unsplash.com/photo-1558470598-a5dda9640f68?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1051&q=80");
    background-repeat: no-repeat;
    background-size: cover;
}
.intro .left {
    flex: 0.5 1;
    text-align: center;
}
.intro .left img {
    object-fit: cover;
}
.intro .right {
    flex: 0.5 1;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 50%;
    font-family: 'Acme',  sans-serif;
    font-size: 2em;
}
.intro .right .social_media {
    margin-top: 20px;
    font-size: 25px;
    width: 40%;
    display: flex;
    justify-content: space-between;
}
.intro .right .social_media a {
    color: #000;
}
.intro .right h1 {
    font-size: 4rem;
    margin: 30px auto;
}
.intro .right .link_next {
    font-size: 3rem;
    position: absolute;
    bottom: -100px;
    color: #000;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    -webkit-animation: blinkicon 2s infinite;
            animation: blinkicon 2s infinite;
}
@-webkit-keyframes blinkicon {
    100% {
    opacity: 0;
}
}
@keyframes blinkicon {
    100% {
    opacity: 0;
}
}@media screen and (max-width: 900px) {
    .intro {
    justify-content: center;
    align-items: center;
}
.intro .left {
    flex: 0 1;
    display: none;
}
.intro .right {
    width: 100%;
    flex: 0.7 1;
    font-family: 'Acme',  sans-serif;
    align-items: center;
    text-align: center;
    font-size: 1.5rem;
}
.intro .right .social_media {
    margin: 10px auto;
}
.intro .right .social_media a {
    padding-right: 10px;
}
.intro .right h1 {
    font-size: 3rem;
    margin: 40px auto;
}
}

